import React from 'react';
import Navbar from './navbar';
import Footersec from './footer';
import {HashRouter, Route, Switch} from 'react-router-dom';
import Contact from './Contact';
import Reccomendation from './Giverec';
import Homepage from './Homepage';
import Newblog from './Blogwrite';
import Construction from './construction';
import {Provider} from './contextaAPI';
import Newproject from './projectwrite';
import Projectpage from './projectpage';
import Blogpage from './blogpage';
import Showcaseblogs from './allblogspage';
import Showcaseprojects from './allprojectspage';
import Scrolltotop from './scrolltotop';

function App() {
  return (
    <Provider>
      <HashRouter>
      <Scrolltotop />
      <Navbar/>
      <Switch>
        <Route exact path="/" component={Homepage}></Route>
        <Route exact path="/contactpage" component={Contact}></Route>
        <Route exact path="/write-a-reccomendation" component={Reccomendation}></Route>
        <Route exact path="/addnewblog" component={Newblog}></Route>
        <Route exact path="/addnewproject" component={Newproject}></Route>
        <Route exact path="/allblogs" component={Showcaseblogs}></Route>
        <Route exact path="/allprojects" component={Showcaseprojects}></Route>
        <Route exact path="/project/:id" component={Projectpage}></Route>
        <Route exact path="/blog/:id" component={Blogpage}></Route>
        <Route component={Construction}></Route>
      </Switch>
      <Footersec />
    </HashRouter>
    </Provider>
  );
} 

export default App;
