import React from 'react';
import { Consumer } from './contextaAPI';
import './App.css';
import ReactMarkdown from 'react-markdown';

export default function Blogpage(props) {
    return(
        <Consumer>
            {(value) =>{
                const {Blogs} = value;
                const id = props.match.params.id;
                const blog = Blogs.filter(function (blog)
                {
                    return blog.id == id;
                })[0];
                let {title, imageUrl, cardtext} = blog;
                return (
                    <div className="container-fluid my-5 py-5 text-center center-img">
                        <img className="pt-5" src={imageUrl} alt={title}></img>
                        <h1 className="pt-5">{title}</h1>
                    </div>
                );
            }}
        </Consumer>
    );
}
