import React from 'react';
import { Consumer } from './contextaAPI';
import { Link } from 'react-router-dom';

export default function Showcaseblogs() {
    return(
        <Consumer>
            {(value) => {
                const {Blogs} = value;
                return (
                    <div className="container my-5 py-5">
                        <h1 className="text-center fw-light py-5">All<span className="text-info"> Blogs</span></h1>
                        <div className="row my-5 py-3 px-2 px-md-5">
                            {
                            Blogs.map((blog) => (
                                <div key={blog.id} className="col-12 col-md-6 py-3">
                                    <div className="card shadow h-100 center-img">
                                    <img className="card-img-top w-100" src={blog.imageUrl} alt="" style={{height: "70%"}}></img>
                                        <div className="card-body">
                                            <h4 className="card-title">{blog.title}</h4>
                                            <p className="card-text">{blog.cardtext}</p>
                                            <Link to={`/blog/${blog.id}`} className="stretched-link"></Link>
                                        </div>
                                    </div>
                                </div>
                             ) )
                            }
                        </div>
                    </div>
                );
            }}
        </Consumer>
    );
}
