import React from 'react';
import { Link } from 'react-router-dom';
import BlogCards from "./Blogcards";
import {Consumer} from './contextaAPI';

function Blogsec(){
    return(
        <Consumer>
        {(value) =>{
            const {Blogs} = value;
            return(
                <div className="container my-3 pt-5 pb-3 text-center">
                    <h1 className="font-weight-light text-dark display-6">My <span className="text-info">Blogs</span></h1>
                    <p className="lead">I share my views on science and technology in these blogs.</p>
                    <div className="row my-4 py-4">
                    {
                            Blogs.slice(0,3).map((blog) => (
                                <div key={blog.id} className="col-12 col-md-4 py-3">
                                    <BlogCards
                                        blog = {blog}
                                    />
                                </div>
                             ) )
                        }
                    </div>
                    <div className="pe-2 text-end">
                        <Link to="/allblogs" className="text-dark" style={{textDecoration: "none"}}>
                            <h4 className="fw-normal">More Blogs
                                <i className="ms-1 fas fa-arrow-right" style={{fontSize: "80%"}}></i></h4>
                        </Link>
                    </div>
                </div>
            );
        }}
        </Consumer>
    );
}

export default Blogsec;