import React from "react";
import { Link } from "react-router-dom";

function Navbar() {
    
    return(
        <nav className="navbar bg-white fixed-top py-3">
            <div className="container-fluid px-md-5 px-3">
                <Link to="/" className="text-dark fw-normal navbar-brand ps-md-5">Harshil Bhanushali</Link>
                <Link to="/contactpage" className="ms-auto">
                    <button className="btn btn-outline-info text-info">Contact Me</button>
                </Link>
                <button 
                className="navbar-toggler mx-2"
                type="button"
                data-bs-toggle="collapse"
                data-bs-target="#collapseNav">
                <span className="fas fa-bars"></span></button>
                <div className="collapse navbar-collapse flex-grow-0 panel-body" id="collapseNav">
                    <div className="navbar-nav">
                        <li className="nav-item">
                            <Link to="/allblogs" className="nav-link text-dark text-end pe-4 pe-md-auto">Blogs</Link>
                        </li>
                        <li className="nav-item">
                            <Link to="/allprojects" className="nav-link text-dark text-end pe-4 pe-md-auto">Projects</Link>
                        </li>
                        <li className="nav-item">
                            <Link to="/addnewblog" className="nav-link text-dark text-end pe-4 pe-md-auto">Add New Blog</Link>
                        </li>
                        <li className="nav-item">
                            <Link to="/addnewproject" className="nav-link text-dark text-end pe-4 pe-md-auto">Add New Project</Link>
                        </li>
                    </div>
                </div>
            </div>
        </nav>
    );
}

export default Navbar;