import React from 'react';
import { Consumer } from './contextaAPI';
import './App.css';
import ReactMarkdown from 'react-markdown';

export default function Projectpage(props) {
    return(
        <Consumer>
            {(value) =>{
                const {projects} = value;
                const id=props.match.params.id;
                const project = projects.filter(function (project)
                {
                    return project.id == id;
                })[0];
                console.log(project);
                let {title, imageUrl, cardtext} = project;
                return (
                    <div className="container-fluid my-5 py-5 text-center center-img">
                        <img className="pt-5" src={imageUrl} alt={title}></img>
                        <h1 className="pt-5">{title}</h1>
                    </div>
                );
            }}
        </Consumer>
    );
}
