import { event } from 'jquery';
import React, {Component} from 'react';

class Contact extends React.Component {
    state={
        name: "",
        email: "",
        desc: "",
        isShow: false,
    };

    onChange = (event) => {
        this.setState({
            [event.target.name]: event.target.value,
        });
    };

    onSubmit = (event) => {
        event.preventDefault();
        let greet = document.getElementById('greet');
        greet.style.visibility = 'visible';
    };

    render(){
        let {name, email} = this.state;
    return (
        <div className="container text-center my-5 py-3">
            <div>
                <h1 className="fw-light mb-5"><span className="text-info">Thank You!</span> for your interest</h1>
            </div>
            <div className="row justify-content-center">
                <div className="col-11 col-lg-6">
                    <form>
                        <div className="form-group my-3">
                            <label htmlFor="name" className="float-start ps-1">Name *</label>
                            <input type="text" name="name" className="form-control" onChange={this.onChange}></input>
                        </div>
                        <div className="form-group my-3">
                            <label htmlFor="email" className="float-start ps-1">Email *</label>
                            <input type="email" name="email" className="form-control" onChange={this.onChange}></input>
                        </div>
                        <div className="form-group my-3">
                            <label htmlFor="desc" className="float-start ps-1">Tell me About Your Project *</label>
                            <textarea name="desc" rows="5" className="form-control" style={{resize: "none"}} onChange={this.onChange}></textarea>
                        </div>
                        <button type="submit" className="btn btn-dark float-end d-block" onClick={this.onSubmit}>Let's Talk Business!</button>
                    </form>
                </div>
                <h3 id="greet" className="lead my-3 p-3" style={{visibility: 'hidden'}}>Hey {name}! Thanks for Reaching Out. I will contact you shortly at {email}.</h3>
            </div>
        </div>
    )
}}

export default Contact;