import React, {Component} from 'react';
import SimpleMDE from "react-simplemde-editor";
import "easymde/dist/easymde.min.css";
import "./App.css";
import ReactMarkdown from "react-markdown";
import {Consumer} from './contextaAPI';
import {v4 as uuid} from 'uuid';

class Newproject extends React.Component {
    state={
        imageUrl: "",
        Title: "",
        excerpt: "",
        body: "",
        isShow: false,
    };

    onChange = (event) => {
        this.setState({
            [event.target.name]: event.target.value,
        });
    };

    onSubmit = (Handler, event) => {
        event.preventDefault();
        let projectmessage = document.getElementById('newproject');
        projectmessage.style.visibility = 'visible';

        const {imageUrl, Title, excerpt} = this.state;
        const Newproject = {
            id: uuid(),
            title: Title,
            imageUrl: imageUrl,
            cardtext: excerpt,
        };

        Handler("ADD_Project", Newproject);
    };

    onHandle = (value) => {
        this.setState({body: value,});
    };

    render(){
        return(
            <Consumer>
                {(value) =>{
                    const {Handler} = value;
                    let {imageUrl, Title, body} = this.state;
                    return (
                        <div className="container-fluid text-center my-5 py-3">
                            <div>
                                <h1 className="fw-light mb-5">Add New <span className="text-info">Project!</span></h1>
                            </div>
                            <div className="row justify-content-center">
                                <div className="col-11 col-lg-6">
                                    <form>
                                        <div className="form-group my-3">
                                            <label htmlFor="imageUrl" className="float-start ps-1">Featured Image URL *</label>
                                            <input type="text" name="imageUrl" className="form-control" onChange={this.onChange}></input>
                                        </div>
                                        <div className="form-group my-3">
                                            <label htmlFor="Title" className="float-start ps-1">Title *</label>
                                            <input type="email" name="Title" className="form-control" onChange={this.onChange}></input>
                                        </div>
                                        <div className="form-group my-3">
                                            <label htmlFor="excerpt" className="float-start ps-1">Excerpt *</label>
                                            <textarea name="excerpt" rows="2" className="form-control" style={{resize: "none"}} onChange={this.onChange}></textarea>
                                        </div>
                                        <SimpleMDE onChange={this.onHandle} className="text-start" />
                                        <button type="submit" className="btn btn-dark form-control d-block" onClick={this.onSubmit.bind(this, Handler)}>Add New Project!</button>
                                    </form>
                                </div>
                                <div className="col-12 col-lg-6 center-img">
                                    <img src={imageUrl} alt={Title} className="px-3 mb-4 justify-content-center"></img>
                                    <h1 className="fw-normal float-center text-center w-100">{Title}</h1>
                                    {/* <p className="px-2 mt-3 float-start">{body}</p> */}
                                    <ReactMarkdown className="text-start">{body}</ReactMarkdown>
                                </div>
                            </div>
                            <h3 id="newproject" className="lead my-3 p-3 text-center" style={{visibility: 'hidden'}}>Blog added Succesfully.</h3>
                        </div>
                    );
                }}
            </Consumer>
        );
}}

export default Newproject;